import React from 'react';
import 'antd/dist/antd.css';
import Table from '../components/CRUDTable';

import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';
import { Spin } from 'antd';
import { Panel, ListItem, Button, Group, Div, Avatar, PanelHeader } from '@vkontakte/vkui';

import { storeFollowers } from '../store/Followers';
import { storeGroup } from '../store/Groups';
import { getParamsUrl } from '../utils/index';

const { getFollowers, getFollowerById } = storeFollowers;
const { getGroupById } = storeGroup;

const columns = [{
		title: "Номер",
		dataIndex: 'num',
	}, {
		title: "Название",
		dataIndex: 'name',
        render:(name, obj) => <a href={"https://vk.com/id"+obj.followerId}>{name}</a>,
		editable: false,
	},{
		title: "Лайки",
		dataIndex: 'likes',
		sorter: true,
		editable: false,
	},{
		title: "Репосты",
		dataIndex: 'reposts',
		sorter: true,
		editable: false,
	}, {
		title: "Комментарии",
		dataIndex: 'comments',
		sorter: true,
		editable: false,
	}, {
		title: "Баллы",
		dataIndex: 'balls',
		sorter: true,
		editable: false,
}];
class Home extends React.Component {
    componentDidMount() {
        var groupId = getParamsUrl("vk_group_id");
        var userId = getParamsUrl("vk_user_id");
        this.props.getFollowers(groupId);
        this.props.getGroupById(groupId);
        this.props.getFollowerById(groupId,userId);
    }
    render() {
        const { id, go, fetchedUser, followers, followers_count, followers_loading, select_group, getFollowers, myAccount } = this.props;
        var groupId = getParamsUrl("vk_group_id");
        console.log("myAccount", myAccount);
        if (select_group==null) {
            return <Panel id={id}>
                <PanelHeader>Рейтинг подписчиков</PanelHeader>
                <h1 style={{ paddingTop:200, "text-align": "center" }}>
                            Нет информации о данной группе
                        </h1>
                    </Panel>
        }

        return <Panel id={id}>
            <PanelHeader>Рейтинг подписчиков</PanelHeader>
                    {fetchedUser &&
                        <Group title="Вы вошли как">
                            <ListItem
                                before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200} /> : null}
                                description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
                            >
                                {`${fetchedUser.first_name} ${fetchedUser.last_name}`}
                            </ListItem>
                         </Group>}
                    {select_group.name &&
                        <Group title="Группа">
                            <ListItem
                                before={select_group.photoOriginal ? <Avatar src={select_group.photoOriginal} /> : null}
                                description={myAccount && `Вы на ${myAccount.num} месте`}
                            >
                                <a href={"https://vk.com/" + select_group.screenName}>{`${select_group.name}`}</a>
                            </ListItem>
                        </Group>}
                    {select_group.name &&
                        <Group title="Рейтинг подписчиков">
                            <Spin spinning={followers_loading}>
                                <Table
                                    isOpenModal={false}
                                    titleModal={"Подписчики"}
                                    createItem={(ingredient) => { }}
                                    updateItem={(ingredient) => { }}
                                    getItems={(page, sizePage, fields, sort) => {
                                        getFollowers(groupId,{ page, sizePage, fields, sort });
                                    }}
                                    isEditable={false}
                                    items={followers}
                                    keyId={"followerId"}
                                    deleteItem={(ingredient) => { }}
                                    closeModal={() => { }}
                                    totalCount={followers_count}
                                    isPagination={true}
                                    columns={columns}
                                />
                            </Spin>
                        </Group>}
                </Panel>
    }
}

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};
function mapStateToProps(state) {
    return {
        followers: state.followers.followers,
        followers_count: state.followers.followers_count,
        followers_loading: state.followers.followers_loading,
        select_group: state.groups.select_group,
        myAccount: state.followers.myAccount,
    };
}
function matchDispatchToProps(dispatch) {
    return bindActionCreators({ getFollowers, getGroupById, getFollowerById }, dispatch);
}
export default connect(mapStateToProps, matchDispatchToProps)(Home);
