import { message } from 'antd';
import axios from '../actions/ajax';

import { host } from '../utils';

const VK = window.VK;
const ISLOGIN = 'ISLOGIN';
const LOGIN = 'LOGIN';
const LOGOUT = 'LOGOUT';
const ACCOUNT = 'ACCOUNT';
const ACCOUNT_BALANCE = 'ACCOUNT_BALANCE';

const initialState = {
    account: {},
	auth:"",
	isAuth:false,
}; 
export const storeAuth = {
    getUserInfo: () => async (dispatch, getState) => {
        axios.get(host + "/account/getUserInfo", {})
            .then((result) => {
                result = result.data;
                if (result.isSuccess)
                    dispatch({ type: ACCOUNT, payload: result.object });
                else {
                    document.cookie = "token=" + 0 + "; path=/; expires=Mon, 01-Jan-2010 00:00:00 GMT";
                    window.location.href = "http://localhost:50291/";
                }
            });
    },
    isloginVK:() => async (dispatch, getState) => {
		VK.Auth.getLoginStatus(response=>{
			if(!response.session){
				dispatch({ type: ISLOGIN, payload: false  });
			} else {
				VK.Api.call('users.get', {fields:"photo_100",v:"5.73"}, function(r) {
                    if (r.response) {
						dispatch({ type: LOGIN, payload: r.response[0]  });
					}
				});
			}
		})
	},
    loginVK:() => async (dispatch, getState) => {
        VK.Auth.login((response) => {
            console.log(response.session.user);
			dispatch({ type: LOGIN, payload: response.session.user  });
        }, 262144);
	},
    logoutVK:() => async (dispatch, getState) => {
		VK.Auth.logout((response)=>{
			dispatch({ type: LOGOUT, payload: {} });
		});
	},
};

export const reducer = (state = initialState, action) => {
    switch (action.type) {
        case LOGIN: return { ...state, auth: action.payload, isAuth: true  }
        case ISLOGIN: return { ...state,  isAuth: action.payload  }
        case LOGOUT: return { ...state,auth:null, isAuth: false }
        case ACCOUNT_BALANCE: return {
            ...state, account: { ...state.account, balance: action.payload  }
        }
        case ACCOUNT: return {
            ...state, account: action.payload 
        }
        default:
            return state;
    }
};
