import { message } from 'antd';

import axios from '../actions/ajax';
import { host } from '../utils';
import { setInterval } from 'core-js';

const VK = window.VK;

const SELECTGROUP = 'SELECTGROUP';

const initialState = {
    select_group: {},
};
export const storeGroup = {
    getGroupById: (socialId) => async (dispatch, getState) => {
        
        axios.get(host + "/group/GetGroupById", {
            params: {
                socialId
            }
        })
        .then((result) => {
            result = result.data;
            if (result.isSuccess) {
                
                dispatch({ type: SELECTGROUP, payload: result.object });
            } else {
                dispatch({ type: SELECTGROUP, payload: null });
            }

        }).catch(() => dispatch({ type: SELECTGROUP, payload: null }));

        
    }
};

export const reducer = (state = initialState, action) => {
    switch (action.type) {
        
        case SELECTGROUP:
            
            return {
                ...state, select_group: action.payload
            };
        default:
            return state;
    }
};
